
import React, { useEffect, useState, useCallback } from 'react';
import { Outlet, Link } from "react-router-dom";
import '../styles/Layout.css';
import ReactQuill, { Quill } from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import "../styles/Editor.css"
import "../styles/Viewer.css"
import "../styles/NotePreview.css"
import NotePreview from '../elements/NotePreview';
import { v4 as uuidv4 } from 'uuid';


const Layout = () => {

  //
  // Parameters
  //
  const { elementID, mode } = useParams();
  const navigate = useNavigate();
  const options = {
    year: "numeric",
    month: "long",
    day: "numeric",
    hour: "numeric",
    minute: "numeric",
  };
  const formatDate = (when) => {
    const formatted = new Date(when).toLocaleString("en-US", options);
    if (formatted === "Invalid Date") {
        return "";
    }
    return formatted;
  };



  //
  // The States
  //
  const [objects, setObjects] = useState([
    // {
    //   content: "<p>Content</p>",
    //   title: "Title",
    //   id: "1",
    //   date: "today"
    // }
  ])
  const [value, setValue] = useState("")
  const [currTitle, setCurrTitle] = useState("")
  const [currDate, setCurrDate] = useState("")
  const [currContent, setCurrContent] = useState("")
  const [previousObjects, setPreviousObjects] = useState([])
  const [body, setBody] = useState(ChooseBody())



  //
  // The Effects
  //
  useEffect(()=>{
    console.log("setPreviousObject 1")
    setPreviousObjects(objects)
  }, [elementID, mode])

  useEffect(()=>{
    const nextObjects = objects.map(object => {
      if (object.id == elementID) {
        return (
          {
            content: currContent,
            title: object.title,
            id: object.id,
            date: object.date
          }
        )
      } else {
        return object
      }
    })
    setObjects(nextObjects)
    setValue(currContent)
  }, [currContent])

  useEffect(()=>{
    const nextObjects = objects.map(object => {
      if (object.id == elementID) {
        return (
          {
            content: object.content,
            title: currTitle,
            id: object.id,
            date: object.date
          }
        )
      } else {
        return object
      }
    })
    setObjects(nextObjects)
  }, [currTitle])

  useEffect(()=>{
    const nextObjects = objects.map(object => {
      if (object.id == elementID) {
        return (
          {
            content: object.content,
            title: object.title,
            id: object.id,
            date: currDate
          }
        )
      } else {
        return object
      }
    })
    setObjects(nextObjects)
  }, [currDate])
  useEffect(()=>{
    setBody(ChooseBody())
  }, [objects, elementID, mode, currTitle, currDate, value])

  useEffect(()=>{
    const nextTitle = objects.map(object => {
      if (object.id == elementID) {
        return object.title
      } else {
        return null
      }
    })
    let filteredTitleArray = nextTitle.filter(function (el) {
      return el != null;
    });
    setCurrTitle(filteredTitleArray[0])
    const nextDate = objects.map(object => {
      if (object.id == elementID) {
        return object.date
      } else {
        return null
      }
    })
    let filteredDateArray = nextDate.filter(function (el) {
      return el != null;
    });
    setCurrDate(filteredDateArray[0])
    const nextContent = objects.map(object => {
      if (object.id == elementID) {
        return object.content
      } else {
        return null
      }
    })
    let filteredContentArray = nextContent.filter(function (el) {
      return el != null;
    });
    setCurrContent(filteredContentArray[0])
    setValue(filteredContentArray[0])
  }, [objects, body])

  useEffect(()=>{
    let isElement = false
    objects.forEach(element => {
      if (element.id === elementID) {
        isElement = true
      }
    });
    if ((!elementID && !mode) || !isElement) {
      navigate(`./notes`)
    } else if (mode && mode !== "edit") {
      navigate(`./notes/${elementID}`)
    }
  }, [])



  //
  // The Body Functions
  //
  function MyEditor() {

    const modules = {
      toolbar: [
        [{ header: [1, 2, 3, 4, 5, 6, false] }],
        ["bold", "italic", "underline", "strike", "blockquote"],
        [{ font: [] }],
        [{ align: ["right", "center", "justify"] }],
        [{ list: "ordered" }, { list: "bullet" }],
        ["link", "image"],
        [{ color: [] }],
        [{ background: [] }]
      ]
    }

    const formats = [
      "header",
      "bold",
      "italic",
      "underline",
      "strike",
      "blockquote",
      "list",
      "bullet",
      "link",
      "color",
      "image",
      "background",
      "align",
      "font"
    ]
    console.log("Set")
    console.log(objects)
    console.log(previousObjects)

    return (
      <>
        <div className="viewer-header">
          <div className="viewer-header-left">
            <div className="viewer-title">
              <input type="text" placeholder={currTitle} onChangeCapture={e => setCurrTitle(e.target.value)}/>
              </div>
            <div className="viewer-date">
              <input type="datetime-local" id="date_time_edit" autoComplete="on" value={currDate} onChangeCapture={e => setCurrDate(e.target.value)}/> 
            </div>
      
          </div>
          <div className="viewer-header-right">
            <button className="viewer-edit-button" onClick={() => Save()}>Save</button>
            <button className="viewer-delete-button" onClick={() => Delete()}>Delete</button>
            {/* <button className="viewer-edit-button">Save</button> */}
            {/* <button className="viewer-delete-button">Delete</button> */}
          </div>
        </div>
        <ReactQuill formats={formats} modules={modules} theme="snow" value={currContent} onChange={setCurrContent} />
      </>
    )
  }
  function MyViewer() {

    return (
      <>
        <div className="viewer-header">
          <div className="viewer-header-left">
            <div className="viewer-title">{currTitle}</div>
            <div className="viewer-date">{formatDate(currDate)}</div>
          </div>
          <div className="viewer-header-right">
            <button className="viewer-edit-button" onClick={() => Edit()}>Edit</button>
            <button className="viewer-delete-button" onClick={() => Delete()}>Delete</button>
          </div>
        </div>
        <div className="viewer-body">
          <div className="viewer-body-text">
            <div><Outlet context={["other", value, objects]}/></div>
          </div>
        </div>
      </>
    );
  }
  function ChooseBody() {
    if (mode === "edit") {
      // Check for Edit mode
      return MyEditor()
    } else if (elementID) {
      // Check for element
      return MyViewer()
    } else {
      // Use Home
      return (
        <div><Outlet context={["body", value, objects]}/></div>
      );
    }
  }
  function Save() {
    const nextObjects = objects.map(object => {
      if (object.id === elementID) {
        return (
          {
            content: currContent,
            title: currTitle,
            id: object.id,
            date: currDate
          }
        )
      } else {
        return object
      }
    })
    setObjects(nextObjects)
    navigate(`./notes/${elementID}`)
  }
  function Delete() {
    const answer = window.confirm("Are you sure?");
    if (answer) {
      const nextObjects = objects.map(object => {
        console.log(object)
        if (object.id === elementID) {
          console.log("==")
          return null
        } else {
          console.log("!=")
          return object
        }
      })
      let filteredObjects = nextObjects.filter(function (el) {
        return el != null;
      });
      setObjects(filteredObjects)
      navigate(`./notes`)
    }
  }
  function Edit() {
    navigate(`./notes/${elementID}/edit`)
  }
  function Cancel(prevObj, id) {
    setObjects(prevObj)
    navigate(`./notes/${id}`)
  }



  //
  // The Navigation Functions
  //
  function makeElements(item) {
    return (
      <NotePreview text={item.content} title={item.title} date={item.date} isFocus={elementID===item.id} clickBehaviour={()=>{Cancel(previousObjects, item.id)}}></NotePreview>
    )
  } 
  function toggleHidden(id) {
    var x = document.getElementById(id);
    if (x.style.display === "none") {
      x.style.display = "flex";
    } else {
      x.style.display = "none";
    }
  }
  function addNewObject() {
    const newID = uuidv4()
    setObjects(
      [...previousObjects, 
        {
          content: "<p>No Content</p>", 
          title: "Untitled", 
          id: newID, 
          date: "No Selected Date"
          }
      ]
    )
    navigate(`./notes/${newID}/edit`)
  }






  //
  // The output
  //
  return (
    <>
      {/* Header */}
      <header className='main-header'>
        <div className='left'>
          <button onClick={()=>{toggleHidden("navigation-bar")}}>&#9776;</button>
        </div>
        <div className='center'>
          <h1>Lotion</h1>
          <h5>Like Notion, but worse.</h5>
        </div>
        <div className='right'></div>
      </header>

      {/* Body */}
      <div className='body'>

        {/* Navigation Bar */}
        <div className='navigation-bar' id='navigation-bar'>
          <div className='navigation-header'>
            <div>Notes</div>
            <button onClick={()=>{addNewObject()}}>+</button>
          </div>
          <div className='navigation-items'>
            <Outlet context={["nav", value, objects]}/>
            {objects.map(makeElements)}
          </div>
        </div>

        {/* Content */}
        <div className='content-container'>
          {body}
        </div>

      </div>
    </>
  )
};

export default Layout;