import '../styles/Home.css';
import { useOutletContext } from "react-router-dom";
import React from 'react';
import 'react-quill/dist/quill.snow.css';




const Home = () => {


  const [context, value, objects] = useOutletContext();

  if (context == "nav" && objects[0]===undefined) {
    return (
      <div className='nav-placeHolder-container'>
        <div className='nav-placeHolder-text'>No notes yet.</div>
      </div>
    )
  }

  else if (context == "body") {
    return (
    <div className='body-placeHolder-container'>
      <div className='body-placeHolder-text'>Select a note, or create a new one.</div>
    </div>
    )
  }
  else if (context == "other") {
    return (
      <div className='body-container'>
        <div className='body-text'>
          {value}
        </div>
      </div>
    )
  }
}

export default Home;
